/* tablet version */

@media(min-width:700px){


    /* dev */
    *{
        
    }  
      body{
        background-color: lightgreen;
    }
    /* general */


    p{
        font-size: 20px;
    }
    h3{
        font-size:25px;
    }
    h2{
        font-size: 30px;
    }


    /* header */

nav p {
    font-size:28px;
    
}
nav h1 {
    font-size: 35px;
    
}
#nav-options {
    background-color: #000;
    text-align: left;
    height: 60px;
    height: 50px;


}
#nav-options ul{
    font-size: 20px;
    /* list-style: none;  */
    display: inline-flex;
    margin: 0;
    padding: 0;
    border:none;
    height: 50px;
}
#nav-options ul li {
    margin: 17px 0 auto 18px;
}

#nav-options ul li:nth-of-type(1) :after{
    content: "|";
    margin-left:8px;
    color:#f4f3f1;
}
#nav-options ul li:nth-of-type(2) :after{
    content: "|";
    margin-left:8px;
    color:#f4f3f1;
}

#nav-options ul li a {
    color:#f4f3f1;
    font-weight:400;
}
#title{
    padding:15px;
    height: 50px;

}

